














































































import { Vue, Component } from "vue-property-decorator";
import service from "@/services/generalService";
import Header from "@/components/Header.vue";
import Regions from "@/components/Regions.vue";
import Sort from "@/components/Sort.vue";
import { CountryDto } from "@/types/index";

@Component({
  components: {
    Header,
    Regions,
    Sort,
  },
})
export default class Home extends Vue {
  countries: CountryDto[] = [];
  regions: any = [];
  countriesFilter: any = [];
  loader = false;
  searchText = "";
  errorMsg = "";
  mounted() {
    if (localStorage.getItem("Countries")) {
      this.countries = JSON.parse(localStorage.getItem("Countries") || "[]");
      if (this.countries.length !== 0) {
        this.countries.map((item) => {
          item.populationSlash = String(item.population).replace(
            /(\d)(?=(\d\d\d)+(?!\d))/g,
            "$1,"
          );
        });
      }
      this.countriesFilter = this.countries;
      this.setRegions();
    } else {
      this.getAllCountries();
    }
  }
  getAllCountries() {
    this.loader = true;
    service
      .getMethod("all")
      .then((res: any) => {
        this.loader = false;
        this.countries = res;
        if (this.countries.length !== 0) {
          this.countries.map((item) => {
            item.populationSlash = String(item.population).replace(
              /(\d)(?=(\d\d\d)+(?!\d))/g,
              "$1,"
            );
          });
        }
        this.countriesFilter = this.countries;
        localStorage.setItem("Countries", JSON.stringify(this.countries));
        this.setRegions();
      })
      .catch((error: any) => {
        this.loader = false;
        this.errorMsg = error.response.data.message;
      });
  }
  setRegions() {
    this.countries.forEach((item) => {
      this.regions.push(item.region);
    });
    this.regions = [...new Set(this.regions)];
  }
  changeRegion(evt: string) {
    this.loader = true;
    if (evt === "All") {
      this.countriesFilter = this.countries;
    } else {
      this.countriesFilter = this.countries.filter(
        (item) => item.region === evt
      );
    }
    setTimeout(() => {
      this.loader = false;
    }, 100);
  }
  changeSort(evt: any) {
    this.loader = true;
    this.countriesFilter = evt;
    setTimeout(() => {
      this.loader = false;
    }, 100);
  }
  checkEmpty() {
    if (this.searchText === "") {
      this.countriesFilter = this.countries;
    }
  }
  searchByCountry() {
    this.errorMsg = "";
    if (this.searchText === "") {
      this.countriesFilter = this.countries;
    } else {
      this.loader = true;
      service
        .getMethod(`name/${this.searchText}?fullText=true`)
        .then((res: any) => {
          this.loader = false;
          this.countriesFilter = res;
          this.countriesFilter.map((item: any) => {
            item.populationSlash = String(item.population).replace(
              /(\d)(?=(\d\d\d)+(?!\d))/g,
              "$1,"
            );
          });
        })
        .catch((error: any) => {
          this.loader = false;
          this.errorMsg = error.response.data.message;
          this.countriesFilter = [];
        });
    }
  }
  goToDetail(name: string) {
    this.$router.push(`country/${name}/detail`);
  }
}
